import React, { useEffect, useMemo, useState } from 'react';
import { Text, Card, Flex, Link, CardBody } from '@makerdao/ui-components-core';
import { formatter, gemToDotBtc, onlyUnique, prettifyNumber } from 'utils/ui';
import Carat from './Carat';
import styled from 'styled-components';
import { ReactComponent as ExternalLinkIcon } from 'images/external-link.svg';
import useLanguage from 'hooks/useLanguage';
import { watch } from 'hooks/useObservable';
import BigNumber from 'bignumber.js';
import prettyFormat from 'pretty-format';
import _ from 'lodash'
import { useReducer } from 'reinspect';
import useCurrencyConv from 'hooks/useCurrencyConv';

const StyledCardBody = styled(CardBody)`
  cursor: pointer;
`;



const SidebarFeeds = () => {
  const [collapsed, setCollapsed] = useState(true);
  const { lang } = useLanguage();
  const gem = lang.brand;

  const {feeds} = useCurrencyConv()

  return (
    <Card pt="sm" css={'overflow:hidden;'}>
      <Flex justifyContent="space-between" alignContent="center" px="s" pt="">
        <Text t="h4">{lang.sidebar.price_feeds}</Text>
      </Flex>

      <CardBody mt="s2">
        {feeds && feeds.length !== 0 &&
          feeds.map(
            (feed, index) =>
              (!collapsed || index < 4) && (
                <Flex
                  key={`feed_${index}`}
                  justifyContent="space-between"
                  alignItems="baseline"
                  width="100%"
                  py="xs"
                  px="s"
                  bg={index % 2 ? 'coolGrey.100' : 'white'}
                >
                  <Text
                    color="darkLavender"
                    fontWeight="semibold"
                    t="smallCaps"
                  >
                    {feed.symbol}
                  </Text>
                  <Text fontSize="1.4rem" color="darkPurple">
                    {`${feed.value} ${gem}`}
                  </Text>
                </Flex>
              )
          )}
      </CardBody>
      {feeds !== undefined && feeds.length > 4 && (
        <StyledCardBody p="s" onClick={() => setCollapsed(!collapsed)}>
          <Flex justifyContent="center" alignItems="center">
            {collapsed ? (
              <>
                <Text pr="xs">{lang.sidebar.view_more}</Text>
                <Carat />
              </>
            ) : (
              <>
                <Text pr="xs">{lang.sidebar.view_less}</Text>
                <Carat rotation={180} />
              </>
            )}
          </Flex>
        </StyledCardBody>
      )}
    </Card>
  );
};

export default SidebarFeeds;
