import { CurrencyConvContext } from 'providers/CurrencyConverter';
import { useContext } from 'react';

const useCurrencyConv = () => {
  const { converter, setConverter, usd, dotBtc, feeds } = useContext(
    CurrencyConvContext
  );
  return { converter, setConverter, usd, dotBtc, feeds };
};

export default useCurrencyConv;
