import useLanguage from 'hooks/useLanguage';
import React, { createContext, useEffect, useState } from 'react';
import { watch } from 'hooks/useObservable';
import useCdpTypes from 'hooks/useCdpTypes';
import { useReducer } from 'reinspect';
import _ from 'lodash';
import { gemToDotBtc } from 'utils/ui';

export const CurrencyConvContext = createContext();
export default function CurrencyConverter({ children }) {
  const { lang } = useLanguage();
  const usd = lang.usd;
  const dotBtc = lang.brand;
  const { cdpTypesList } = useCdpTypes();
  const [converter, setConverter_] = useState(dotBtc);
  const localConverter = window.localStorage.getItem('converter');
  const prices = watch.collateralTypesPrices(cdpTypesList);

  const feeds = _.uniqBy(prices, 'symbol').map(feed => {
    const payload = {
      symbol: feed.symbol.split('/')[1],
      value: gemToDotBtc(feed)
    };
    return payload;
  });

  const setConverter = value => {
    setConverter_(value);
    window.localStorage.setItem('converter', value);
  };

  if (localConverter) {
    window.localStorage.setItem('converter', converter);
  }

  return (
    <CurrencyConvContext.Provider
      value={{ converter, setConverter, usd, dotBtc, feeds }}
    >
      {children}
    </CurrencyConvContext.Provider>
  );
}
